import { RouterView } from "vue-router";

const Contato = () =>
    import ("@/views/contato/Contato.vue")

const ContatoViews = {
    path: "contato",
    name: "Comtato",
    component: RouterView,
    children: [{
            path: "/contato",
            name: "Contato",
            component: Contato

        },

        {
            path: "/contato2",
            name: "Contato2",
            component: Contato
        }
    ]

}

export default ContatoViews;