import { createRouter, createWebHistory } from 'vue-router'
import HomeViwes from "./home/index"
import About from '../views/About.vue'

import AppContaneir from "../components/AppContainer.vue"
import ContatoViews from "./contato/index"
const routes = [{
        path: '/',
        name: 'Main',
        component: AppContaneir,
        children: [
            HomeViwes,
            ContatoViews

        ]
    },
    {
        path: '/about',
        name: 'About',
        component: About
    }
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router